$royal_blue : #002366;
$border:  1px solid #fff;


.main-container {
  width: 60rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -78%);
  display: flex;
  flex-direction: column;
  z-index: 999;
  @media only screen and (max-width: 48em) {
    width: 50rem;
  }
  @media only screen and (max-width: 36em) {
    width: 30rem;
    top: 50%;
  }
}
.main-container img {
  margin-bottom: 1rem;
  @media only screen and (max-width: 36em) {
    margin-bottom: 3rem;
  }
}

h1{
  text-align: center;
  color: #fff;
}

.main-buttons {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 999;
  @media only screen and (max-width: 36em) {
    flex-direction: column;
  }
  a {
    text-decoration: none;
    @media only screen and (max-width: 36em) {
      margin: 1rem 0;
    }
  }
  h4 {
    padding: 2px 1rem 0px 1rem;
    color: #fff;
    font-family: inherit;
    font-size: 2rem;
    font-weight: 400;
    letter-spacing: 3px;
    background-color: none;
    -webkit-transition: background-color 500ms linear;
      -ms-transition: background-color 500ms linear;
      transition: background-color 500ms linear;
     &:hover {
      cursor: pointer;
      color: #000;
      background-color: #fff;
      -webkit-transition: background-color 500ms linear;
      -ms-transition: background-color 500ms linear;
      transition: background-color 500ms linear;
    }

    @media only screen and (max-width: 48em) {
      letter-spacing: 2px;
    }
  }
  div {
    width: 2px;
    height: 2rem;
    background-color: #fff;

    @media only screen and (max-width: 36em) {
      display: none;
    }
  }
}
