.admin_login_box {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	width: 40rem;
	padding: 4rem;
	background: rgba(0, 35, 102,.4);
	box-sizing: border-box;
	
	border-radius: 1rem;

	@media only screen and (max-width: 75em) {
		transform: translate(-50%,-55%);
	}
	@media only screen and (max-width: 48em) {
		transform: translate(-50%,-60%);
	}
	@media only screen and (max-width: 37.5em) {
		transform: translate(-50%,-65%);
	}
	@media only screen and (max-width: 30em) {
		transform: translate(-50%,-70%);
		width: 30rem;
		padding: 2rem;
	}
}
.admin_login_box h2 {
	margin: 0 0 30px;
	padding: 0;
	color: #FFF;
    text-align: center;
    font-family: inherit;
    font-size: 3.6rem;
    font-weight: 700;
	letter-spacing: 3px;
	@media only screen and (max-width: 30em) {
		margin: 0 0 20px;
		font-size: 2.6rem;
		font-weight: 700;
		letter-spacing: 3px;
	}
}

.admin_login_box .inputBox {
	position: relative;
}
.admin_login_box .inputBox input {
	width: 100%;
	padding: 10px 0;
	font-size: 16px;
	color: #FFF;
	letter-spacing: 1px;
	margin-bottom: 30px;
	border: none;
	border-bottom: 1px solid #FFF;
	outline: none;
	background: transparent;

}
.admin_login_box .inputBox label {
	position: absolute;
	top: 0;
	left: 0;
	padding: 10px 0;
	font-size: 16px;
	color: #FFF;
	pointer-events: none;
	transition: .5s;
	animation: LabelOnLoad 1s forwards 0s ease;
	
	
    
}
@keyframes LabelOnLoad{
    0%{
        transform: rotate(0) translateY(-19px);
        opacity: 0;
        }
    100%{
        transform: rotate(0) translateY(0);
        opacity: 1;
        }
}
.admin_login_box .inputBox input:focus ~ label, 
.admin_login_box .inputBox input:valid ~ label {
	top: -30px;
	left:0;
	color: #03A9F4;
    font-size: 1.8rem;
	letter-spacing: 3px;
	@media only screen and (max-width: 30em) {
		font-size: 1.4rem;
		letter-spacing: 3px;
	}
}
.admin_login_box input[type='submit'] {
	background: transparent;
	border: none;
	outline: none;
	color: #FFF;
    background: rgba(0, 35, 102,.8);
	padding: 10px 20px;
	cursor: pointer;
	border-radius: 5px;
	font-size: 14px;
}
.admin_login_box input[type='submit']:hover {
	background-color: rgba(3, 169, 244, 0.7);
}