$royal_blue : #002366;
$border:  1px solid #fff;

.footer-container {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 6rem;
  display: flex;
  justify-content: space-between;
  z-index: 999999;

  @media only screen and (max-width: 75em) {
      height: 15rem;
      flex-direction: column;
  }
}



.footer-first {

  flex-basis: 20%;
  display: flex;
  justify-content: center;
  align-items: center;

  @media only screen and (max-width: 75em) {
        order: 2;
  }

  h5 {
    margin: 0;
    color: #fff;
    font-size: 1.6rem;
    font-weight: 100;
    letter-spacing: 2px;
    padding: 2px 1rem 0px 1rem;
    border-right: 1px solid #fff;

    &:hover {
      cursor: pointer;
    }
  }
}

.footer-second {
  
  flex-basis: 20%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;

  @media only screen and (max-width: 75em) {
      order: 1;
      flex-direction: column;
  }


  h5 {
    margin: 0;
    font-size: 1.6rem;
    color: #fff;
    font-weight: 100;
    letter-spacing: 2px;
    @media only screen and (max-width: 75em) {
      margin-bottom: 2rem;
    }
  }
  img {
    width: 1.8rem;
    margin: 0 .5rem;
    @media only screen and (max-width: 75em) {
      margin: 0 2rem;
    }
    &:hover {
      cursor: pointer;
    }
  }
}

.footer-third {

  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 5rem;

  @media only screen and (max-width: 75em) {
        order: 3;
        margin: 1rem auto;
        flex-grow: 0;
  }

  h5 {
    margin: 0;
    font-size: 1.6rem;
    color: #fff;
    font-weight: 100;
    letter-spacing: 2px;
    @media only screen and (max-width: 48em) {
      font-size: 1.4rem;
      letter-spacing: 1px;
    }
  }
}
.copyright-container {

  display: flex;
  justify-content: center;
  align-items: center;
}
.volume {
  width: 2.8rem;
  padding-bottom: 2px;
  padding-left: 1rem;
}

.volume:hover {
  cursor: pointer;
}

