.contact {
    margin: auto 0;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &_back {
        position: absolute;
        top: 5rem;
    }
    &_all {
        h2 {
            font-family: "Lato", sans-serif;
            font-size: 4.6rem;
            color: #fff;
            text-transform: uppercase;
            font-weight: 900;
            text-align: center;
            letter-spacing: 1px;
            @media only screen and (max-width: 37.5em) {
              font-size: 2.6rem;
            }
        }
    }
}