$royal_blue : #002366;
$border:  1px solid #fff;

.about {
  margin: auto 0;
    width: 100vw;
    height: 61rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
  &_body {

    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    &_right {
      flex-basis: 50%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      margin-top: 8rem;
      @media only screen and (max-width: 37.5em) {
          flex-basis: 80%;
          margin-top: 0rem;
        }
      &_icons {
        margin-top: 2rem;
       &_fb {
        width: 1.5rem;
        margin-right: 1rem;
       }
       &_ytb {
        width: 2rem;
       }

      }
      &_logo {
        width: 35rem;
        @media only screen and (max-width: 48em) {
            width: 25rem;
        }
        @media only screen and (max-width: 37.5em) {
          margin-top: 5rem;
            width: 20rem;
        }
        @media only screen and (max-width: 30em) {
          margin-top: 2rem;
            width: 20rem;
        }
      }
      &_desc {
        width: 100%;
        height: 100%;
        
        &_header {
          font-family: 'Lato', sans-serif;
          font-weight: 700;
          font-size: 1.8rem;
          color: #ffffff;
          letter-spacing: 1px;
          margin: 2rem 0;
          @media only screen and (max-width: 48em) {
              margin: 1.5rem 0;
              font-size: 1.6rem;
          }
        }
        &_text {
          width: 100%;
          height: 300px;
          font-family: 'Lato', sans-serif;
          font-weight: 300;
          font-size: 1.4rem;
          color: #ffffff;
          margin-bottom: 1rem;
          letter-spacing: 1px;
          overflow-y: auto;
          &::-webkit-scrollbar-track {
            -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
            border-radius: 30px;
            background-color: #F5F5F5;
          }
          &::-webkit-scrollbar
          {
            width: .6rem;
            background-color: #F5F5F5;
            border-radius: 30px;
          }
          &::-webkit-scrollbar-thumb
          {
            border-radius: 30px;
            -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
            background-color: $royal_blue;
          }
          @media only screen and (max-width: 37.5em) {
              font-size: 1.2rem;
          }
        }
      }
    }
  }
  &_planet {
    position: absolute;
    width: 100vw;
  }
}
