.box {
    display: flex;

    
    &_icon {
        width: 2rem;
        margin-right: 1rem;
    } 
    &_text {
        color: #fff;
        font-weight: 500;
        font-size: 1.6rem;
        margin: 2px 0 0 0 ;
        letter-spacing: 1px;
    }
}

.back {
    position: absolute;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: absolute;
    top: 5.5rem;
    left: 5rem;
    z-index: 9999999;
    display: flex;
    @media only screen and (max-width: 37.5em) {
        top: 4.5rem;
    }
    @media only screen and (max-width: 30em) {
        top: 2rem;
        left: 2.5rem;
    }
    a {
        text-decoration: none;
    }
    &:hover {
        cursor: pointer;
    }
}