.clients {
    width: 100vw;
    height: 100vh;
    &_back {
        position: absolute;
        top: 5rem;
    }
    &_all {
        width: 100vw;
        height: 15rem;
        margin: auto 0;
        height: 100%;
        margin: auto 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        h2 {
            font-family: "Lato", sans-serif;
            font-size: 4.6rem;
            color: #fff;
            text-transform: uppercase;
            font-weight: 900;
            text-align: center;
            letter-spacing: 1px;
            @media only screen and (max-width: 37.5em) {
              font-size: 2.6rem;
            }
        }
        p{
            font-family: "Lato", sans-serif;
            font-size: 1.4rem;
            color: #fff;
            font-weight: 500;
            text-align: center;
            letter-spacing: 1px;
            @media only screen and (max-width: 37.5em) {
              margin: 0 2rem;
            }
        }
    }
}