.super_admin {
    position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	width: 40rem;
	padding: 4rem;
	background: rgba(0, 35, 102,.4);
	box-sizing: border-box;
	
	border-radius: 1rem;
    & h2 {
        margin: 0 0 30px;
        padding: 0;
        color: #FFF;
        text-align: center;
        font-family: inherit;
        font-size: 3.6rem;
        font-weight: 700;
        letter-spacing: 3px;
        @media only screen and (max-width: 30em) {
            margin: 0 0 20px;
            font-size: 2.6rem;
            font-weight: 700;
            letter-spacing: 3px;
        }
    }
}