$border:  1px solid #fff;

.event {
    margin-top: 10rem;
    width: 100vw;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

     @media only screen and (max-width: 30em) {
        margin-top: 7.5rem;
    }

    &_head {
        flex-basis: 100%;
        display: flex;
        justify-content: space-between;
        @media only screen and (max-width: 37.5em) {
            flex-direction: column;
        }
    }


    &_settings {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-right: 5rem;
        margin-left: 2rem;

        @media only screen and (max-width: 48em) {
            flex-direction: column;
        }
        @media only screen and (max-width: 37.5em) {
            flex-direction: row;
            justify-content: space-between;
            margin-left: 5rem;
            margin-top: 2.5rem;
        }
        @media only screen and (max-width: 30em) {
            margin-left: 2.5rem;

        }
        

        &_type {


            @media only screen and (max-width: 48em) {

            }

            @media only screen and (max-width: 37.5em) {

            }
            @media only screen and (max-width: 30em) {

            }

            &_controls {
                display: flex;
                justify-content: space-between;
                align-items: center;
                @media only screen and (max-width: 37.5em) {
                    flex-direction: column;
                    justify-content: flex-start;
                    align-items: flex-start;
                }
                &_date {

                }
                & > div {
                    margin: 2px 0;
                }
            }

        }

        

        &_navigate {
            margin: 1.5rem 0;
            display: flex;
            justify-content: flex-end;
            @media only screen and (max-width: 75em) {

            }
            @media only screen and (max-width: 48em) {
                display: flex;
                justify-content: space-between;
                align-self: flex-end;
            }
            @media only screen and (max-width: 37.5em) {

            
            }
            @media only screen and (max-width: 30em) {

    
            }


            &_actions {
                display: flex;
                img {
                    width: 2rem;
                    margin: 0 1rem;
                }
                &_forward {
                    transform: rotate(180deg);
                }

                &_forward {

                }

            }
        }
    }
    
    &_title {
        margin-left: 5rem;
        display: flex;
        flex-direction: column;


        @media only screen and (max-width: 48em) {

        }
        @media only screen and (max-width: 37.5em) {

        }
        @media only screen and (max-width: 30em) {
            margin-left: 2.5rem;
        }
        &_desc {
            font-size: 1.6rem;
            color: #fff;
            font-family: 'Lato', sans-serif;
            font-weight: 300;
            margin: 0;
            padding: 0;
            @media only screen and (max-width: 37.5em) {
                font-size: 1.4rem;
            }
        }
        &_t {
            font-size: 4.6rem;
            color: #fff;
            font-family: 'Lato', sans-serif;
            font-weight: 900;
            margin: 0;
            padding: 0;
            @media only screen and (max-width: 48em) {
                font-size: 2.6rem;
            }
            @media only screen and (max-width: 37.5em) {
                font-size: 1.8rem;
            }
        }
    }
    &_body {
        flex-grow: 1;
        display: flex;
        justify-content: flex-start;
        flex-direction: column;
        position: relative;
        margin-top: 5rem;
        @media only screen and (max-width: 48em) {
            margin-top: 3.2rem;
        }
        @media only screen and (max-width: 37.5em) {
            margin-top: 3.2rem;
        }
        @media only screen and (max-width: 30em) {
            margin-top: 3.2rem;
        }
        
        
        &_elements {
            display: flex;
            align-items: center;
            flex: 1;
            flex-wrap: nowrap;


            &_el {
                width: 30rem;
                height: 30rem;
                margin: 0 5rem;
                transition: transform 500ms ease-in-out;
                position: relative;
                @media only screen and (max-width: 30em) {
                    margin: 0 2.5rem;
                }
                &:hover {
                    cursor: pointer;
                }
                
                &_details {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    border-radius: 1.5rem;
                    background-color: rgba(0, 0, 0, .3);
                    z-index: 3;
                    display: flex;
                    flex-direction: column;
                    padding: 3rem;
                    &_header {
                        flex-basis: 40%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        &_upcoming {
                            font-family: 'Lato', sans-serif;
                            font-size: 1.6rem;
                            color: #1abfe1;
                            font-weight: 900;
                            padding: 0 5px;
                            text-align: center;
                            letter-spacing: 6px;
                            text-shadow: 2px 2px 5px #000;
                            @media only screen and (max-width: 37.5em) {
                                font-size: 1.4rem;
                                letter-spacing: 2px;
                            }
                        }
                        &_date {
                            h3 {
                                font-family: 'Lato', sans-serif;
                                font-size: 3rem;
                                color: #fff;
                                font-weight: 900;
                                span {
                                    font-family: 'Lato', sans-serif;
                                    font-size: 1.6rem;
                                    color: #fff;
                                    font-weight: 700;
                                    margin-left: 1rem;
                                }
                            }
                            h2 {
                                font-family: 'Lato', sans-serif;
                                font-size: 2.2rem;
                                color: #fff;
                                font-weight: 900;
                                letter-spacing: 1px;
                            }
                        }
                    }
                    &_body {
                        flex: 1;
                        &_desc {
                            font-family: 'Lato', sans-serif;
                            font-size: 1.4rem;
                            color: #fff;
                            font-weight: 500;
                            margin-top: 1rem;
                            @media only screen and (max-width: 37.5em) {
                                font-size: 1.2rem;
                            }
                        }
                    }
                    &_footer {
                        flex-basis: 10%;
                        display: flex;
                        &_icon {
                            width: 2.5rem;
                            height: 100%;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            img {
                                width: 1.2rem;
                            }
                        }
                        p {
                            flex: 1;
                            height: 100%;
                            font-family:'Lato', sans-serif;
                            font-size: 1.4rem;
                            font-weight: 500;
                            color: #fff;
                            margin: 3px 0 0 5px;
                        }
                    }
                }
                 &_img {
                    position: absolute;
                    width: 30rem;
                    top: 0;
                    left: 0;
                    z-index: 2;
                }
            }
            
        }
    }
}